 import Tool from './tools.js';

/**
 * @description 
 * @param {Object} obj - showToast
 * @param {Function} successCb - showToast
 * @param {Function} errorCb - showToast
 */
const showToast = (obj = {}, successCb, errorCb) => {
	const title = obj.title || 'title',                       //  icon 
	      icon = obj.icon || 'none',                               // success:  title  7 ()   loading:  title  7  none:  title App
				image = obj.image || '',                                 // 
				mask = obj.mask || false,                                // false
				duration = obj.duration || 1500,                         // 1500
				position = obj.position || 'center';                     // topcenterbottom; title 
	uni.showToast({
	    title,
			icon,
			image,
			mask,
	    duration: 2000,
			position,
			success: function() {
				(typeof successCb === 'function') && successCb();
			},
			fail: function(err) {
				console.log('showToast!', err);
				(typeof errorCb === 'function') && errorCb();
			},
	});
};

/**
 * @description  loading ,  uni.hideLoading 
 * @param {Object} obj - showLoading
 * @param {Function} successCb - showLoading
 * @param {Function} errorCb - showLoading
 */
const showLoading = (obj = {}, successCb, errorCb) => {
	const title = obj.title || '',                              // loading
	      mask = obj.mask || true;                                   // 
	uni.showLoading({
	  title: '',
		mask,
		success: function() {
			(typeof successCb === 'function') && successCb();
		},
		fail: function(err) {
			console.log('showLoading!', err);
			showToast({title: 'showLoading!'});
			(typeof errorCb === 'function') && errorCb();
		},
	});
};

/**
 * @description (alertconfirm)--
 * @param {Object} obj - showModal
 * @param {Function} successCb - showModal(  ,   )
 * @param {Function} errorCb - showModal
 */
const showModal = (obj = {}, successCb, errorCb) => {
	const title = obj.hasOwnProperty('title') ? obj.title : '',                                  // 
	      content = obj.content || '',                              // 
				showCancel = obj.hasOwnProperty('showCancel') ? obj.showCancel : true,                          // 
				cancelText = obj.cancelText || '',                        //  4 
				cancelColor = obj.cancelColor || '#000000',                   // 
				confirmText = obj.confirmText || '',                      //  4 
				confirmColor = obj.confirmColor || '#3CC51F';                 // 
	uni.showModal({
		title,
		content,
		showCancel,
		cancelText,
		cancelColor,
		confirmText,
		confirmColor,
		success: function (res) {
			if (res.confirm) {
				// 
				(typeof successCb === 'function') && successCb('');
			} else if (res.cancel) {
				// 
				(typeof successCb === 'function') && successCb('');
			}
		},
		fail: function(err) {
			console.log('showModel!', err);
			showToast({title: 'showModal!'});
			(typeof errorCb === 'function') && errorCb();
		}
	});
};

/**
 * @description 
 * @param {Object} obj - showActionSheet
 * @param {Function} successCb - showActionSheet
 * @param {Function} errorCb - showActionSheet
 */
const showActionSheet = (obj = {}, successCb, errorCb) => {
	const itemList = obj.itemList || [],                           // Array<String>
	      itemColor = obj.itemColor || '#000000',                  // 
				popover = obj.popover || {};                             // (APP)
	uni.showActionSheet({
		itemList,
		itemColor,
		popover,
		success: function (res) {
			// {Number} res.tapIndex--0
			(typeof successCb === 'function') && successCb(res.tapIndex);
		},
		fail: function(err) {
			console.log('showActionSheet!', err);
			showToast({title: 'showActionSheet!'});
			(typeof errorCb === 'function') && errorCb();
		},
	});
};

/**
 * @description --onLoad(tabBar)
 * @param {Object} obj - navigateTo
 * @param {Object} events - 2.8.9+ --
 * @param {Function} successCb - navigateTo
 * @param {Function} errorCb - navigateTo
 */
const navigateTo = (obj = {}, events = {}, successCb, errorCb) => {
	if (!obj.url) {
		showToast({title: 'url'});
		return;
	}
	let _url = obj.url;
	// : /pages/index/index?a=1&b=2
	if (!!obj.params) {
		let str = '';
		for (let key in obj.params) {
			str += `${key}=${obj.params[key]}&`;
		}
		_url = `${_url}?${str.slice(0, str.length - 1)}`;
	}
	const url = _url,                                         //  tabBar  , 
	      animationType = obj.animationType || 'pop-in',      // 
				animationDuration = obj.animationDuration || 300;   //  ms
	uni.navigateTo({
	    url,
			animationType,
			animationDuration,
			events,   // 
			success: function(res) {
				// ,eventChannel
				// urlsuccessCb res.eventChannel.emit('', ) ; this.getOpenerEventChannel().on
				(typeof successCb === 'function') && successCb(res);
			},
			fail: function(err) {
				console.log('navigateTo!', err);
				showToast({title: 'navigateTo!'});
				(typeof errorCb === 'function') && errorCb();
			},
	});
};

/**
 * @description  getCurrentPages() 
 * @param {Object} obj - navigateBack
 */
const navigateBack = (obj = {}) => {
	const delta = obj.delta || 1,                               //  delta 
				animationType = obj.animationType || 'pop-out',       // 
				animationDuration = obj.animationDuration || 300;     //  ms
	uni.navigateBack({
	   delta,
		 animationType,
		 animationDuration
	});
};

/**
 * @description 
 * @param {Object} obj - 
 * @param {Function} successCb - 
 * @param {Function} errorCb - 
 */
