/*********************************************************************
 * -
 * 
 * 1 / /
 * 2 {timeStamp, nonceStr, package, paySign}
 * 3API wxPay()
 *********************************************************************/

import {mapState, mapGetters} from 'vuex';
export default {
  computed: {
		...mapState({
			openid: state => state.userInfo.openid,
			sessionKey: state => state.userInfo.sessionKey
		}),
		...mapGetters({
			token: 'getToken'
		})
	},
	data() {
		return {
			providerCN: {
				alipay: '',
				wxpay: ''
			}
		};
	},
	methods: {
		/**
		 *  / 
		 * @param {String} provider -  alipay/wxpay
		 * @param {Function} successCb  - 
		 */
		getProvider(provider, successCb) {
			uni.getProvider({
				service: 'payment',
				success: (e) => {
					console.log(e);
					// e.provider - 
					if (e.provider.includes(provider)) {
						typeof successCb === 'function' && successCb();
					} else {
						this.$uniTools.showToast({title: `${this.providerCN[provider]}${this.providerCN[provider]}`});
					}
				}
			});
		},
		/**
		 * 
		 * @param {Object} config -  {timeStamp, nonceStr, package, paySign}
	   * @param {Function} successCb  - 
		 * @param {Function} errorCb - 
		 */
    wxPay(config, successCb, errorCb) {
			uni.requestPayment({
				provider: 'wxpay',
				timeStamp: config.timeStamp,
				nonceStr: config.nonceStr,
				package: config.package,
				signType: 'MD5',
				paySign: config.paySign,
				success: successCb,
				fail: (err) => {
					this.$uniTools.showToast({title: ',', success: errorCb});
					console.log('fail:' + JSON.stringify(err));
				}
			});
		}
	}
};
